












































































































































































































































import goodsCard from 'template/goods_card'
import loading from 'template/loading'
export default {
	components: {
		goodsCard,
		loading
	},
	data() {
		return {
			popUp: false,
			isInvoice: false,
			order: {},
			goods: [],
			address: {},
			remark: '',
			isUpdate: false,
			invoiceType: 1,
			invoiceData: {},
			invoiceText: '本次不开具发票',
			payPop: false,
			payway: 1,
			coin: 0,
			realCost: 0,
			field: 1,
			isLoading: false,
		};
	},
	onShow() {
		if (this.isUpdate) {
			this.getOrderInfo()
		}
	},
	onLoad(options) {
		console.log(options.field)
		this.field = options.field
		if (options.field == 2) {
			this.payway = 4
		}
		this.getOrderInfo()
	},
	methods: {
		getOrderInfo() {
			var that = this
			this.isLoading = true
			this.$api.post('/api/order/cartOrder', {
				address_id: this.address.id,
				invoice_info: ''
			}, function(res) {
				console.log(res)
				that.isLoading = false
				if (res.data.code == 200) {
					var data = res.data.data
					that.order = data
					that.realCost = data.amount
					that.goods = data.goods_info
					that.address = data.address
				} else {
					uni.showToast({
						title: res.data.msg,
						mask: true,
						icon: 'none'
					});
				}
			})
		},

		choosePay() {
			this.payPop = true
			this.popUp = true
		},

		changePay(op) {
			this.payway = op
			if (op == 1) {
				this.realCost = this.order.amount
			}
			if (op == 2 || op == 5) {
				this.coin = this.order.amount
			}
			if (op == 3 || op == 4 || op == 6) {
				this.coin = 0
			}
		},

		//输入购物币并判断合理性
		getCoin(e) {
			// this.coin = e.detail.value
			var val = e.detail.value
			var totalCoin = this.order.cur_coin * 100 / 100

			var idx = String(val).indexOf(".") + 1; //获取小数点的位置

			var count = String(val).length - idx; //获取小数点后的个数
			if (idx > 0 && count > 2) {
				uni.showToast({
					title: "最多输入两位小数，请重新输入",
					icon: "none"
				})
			} else if (val > totalCoin) {
				uni.showToast({
					title: "已超过账户可用购物币，请重新输入",
					icon: "none"
				})
			} else if (val > this.order.amount) {
				uni.showToast({
					title: "已超过订单金额，请重新输入",
					icon: "none"
				})
			} else {
				this.coin = val
				this.realCost = this.order.amount * 100 / 100 - this.coin
			}
		},

		//确认订单
		confirmOrder() {
			var that = this
			if (this.payway == 2) {
				if (this.order.cur_coin < this.order.amount) {
					return uni.showToast({
						title: '当前账户购物币不足，请选用其他方式',
						icon: 'none'
					})
				}
				uni.showModal({
					title: '',
					content: '确认使用' + that.coin + '个购物币支付订单吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.generateOrder()
						}
					}
				})
			} else if (this.payway == 3) {
				if (this.coin == 0) {
					return uni.showToast({
						title: '请输入购物币数量',
						icon: 'none'
					})
				}
				uni.showModal({
					title: '',
					content: '确认使用' + that.coin + '购物币支付订单吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.generateOrder()
						}
					}
				})
			} else if (this.payway == 4) {
				if (this.order.cur_point < this.order.point_total) {
					return uni.showToast({
						title: '当前账户积分不足，请选择其他支付方式',
						icon: 'none'
					})
				}
				that.generateOrder()

			} else if (this.payway == 5) {
				if (this.order.cur_point < this.order.point_total) {
					return uni.showToast({
						title: '当前账户积分不足，请选择其他支付方式',
						icon: 'none'
					})
				}
				if (this.order.cur_coin < this.order.amount) {
					return uni.showToast({
						title: '当前购物币不足，请选择其他支付方式',
						icon: 'none'
					})
				}
				uni.showModal({
					title: '',
					content: '确认使用' + this.order.point_total + '积分+' + this.order.amount + '购物币支付订单吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.generateOrder()
						}
					}
				})
			} else if (this.payway == 6) {
				if (this.order.cur_point < this.order.point_total) {
					return uni.showToast({
						title: '当前账户积分不足，请选择其他支付方式',
						icon: 'none'
					})
				}
				that.generateOrder()
			} else {
				that.generateOrder()
			}
		},

		//生成订单
		generateOrder() {

			if (!this.address.id && !this.order.all_sale_flag) {
				return uni.showToast({
					title: '请添加一个地址',
					icon: 'none'
				})
			}
			var that = this
			this.$api.post('/api/order/generateOrder', {
				pay_way: this.payway,
				coin: this.coin,
				address_id: this.address.id,
				remark: this.remark,
				invoice_info: this.invoiceData.title ? JSON.stringify(this.invoiceData) : ''
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) { //含现金的支付
					var order_id = res.data.data.order_id
					that.doPay(order_id)
				} else if (res.data.code == 201) { //纯购物币支付
					var order_id = res.data.data.order_id
					uni.showToast({
						title: '下单成功！',
					})
					setTimeout(function() {
						uni.redirectTo({
							url: '/pages/pay/pay_success/pay_success?id=' + order_id,
						})
					}, 500)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},

		//支付
		doPay: function(order_id) {
			var that = this
			this.$api.post('/api/miniprogram.payment/unifiedOrder', {
				order_id: order_id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var payObj = res.data.data;
					payObj.success = function() {
						//todo 这里写支付成功后的逻辑操作
						that.isUpdate = false
						setTimeout(function() {
							uni.redirectTo({
								url: '/pages/pay/pay_success/pay_success?id=' + order_id,
							})
						}, 300)
					}
					payObj.fail = function() {
						//todo 这里写取消支付的逻辑
						uni.showToast({
							title: '您取消了支付',
							icon: 'none'
						})
						setTimeout(function() {
							uni.redirectTo({
								url: '/pages/user/orders/order-detail/order-detail?id=' + order_id,
							})
						}, 500)
					}
					uni.requestPayment(payObj)
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none'
					})
				}

			})
		},

		//提交发票信息
		formSubmit(e) {
			console.log(e.detail.value)
			var form = e.detail.value
			var data = {
				invoice_type: 1,
				title_type: this.invoiceType,
				title: form.title,
				tax_no: form.tax_no,
				bank: form.bank,
				bank_no: form.bank_no,
				address: form.address,
				phone: form.phone
			}
			console.log(data)
			if (this.invoiceType == 1) {
				if (data.title == '' || data.tax_no == '') {
					return uni.showToast({
						title: '星号项为必填项',
						icon: 'none'
					})
				}
			}
			if (this.invoiceType == 2) {
				if (data.title == '') {
					return uni.showToast({
						title: '星号项为必填项',
						icon: 'none'
					})
				}
			}

			this.invoiceData = data
			this.invoiceText = this.invoiceType == 1 ? '企业发票' : '个人发票'
			this.hidePop()
		},

		//不使用发票
		noInvoice() {
			this.invoiceData = {}
			this.hidePop()
			this.invoiceText = '本次不开具发票'
		},

		//发票弹窗
		setInvoice() {
			this.isInvoice = !this.isInvoice
		},

		//发票类型
		changeInvType(op) {
			this.invoiceType = op
			console.log(this.invoiceType)
		},
		//留言
		getRemark(e) {
			this.remark = e.detail.value
		},

		hidePop() {
			this.popUp = false
			this.isInvoice = false
			this.payPop = false
		}
	}
}
